	module.exports = {"data":{"markdownRemark":{"html":"<p>Application Programming Interfaces - are tools that computers use to communicate with one another.</p>\n<p>Most web APIs transfer data in a format called JSON. JSON stands for JavaScript Object Notation. JSON is nothing more than object properties and their current values, sandwiched between a <code>{</code> and a <code>}</code>.</p>\n<p>These properties and their values are often referred to as “key-value pairs”.</p>\n<p>Here is a sample of what it looks like.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$.getJSON(\"/json/cats.json\", function(json) {\n\n   $(\".message\").html(JSON.stringify(json));\n\n });</code></pre>\n      </div>","fields":{"slug":"/certificates/get-json-with-the-jquery-getjson-method/"},"frontmatter":{"title":"Get JSON with the jQuery Getjson Method"}}},"pathContext":{"slug":"/certificates/get-json-with-the-jquery-getjson-method/"}}

